import { render } from "./renderer";
import type { FinderProps } from "../shared/types/forms/finder";


export default (() => {
  const launch = function (formData: React.ComponentType<FinderProps>) {
    try {
      const initialResults = JSON.parse(
          // @ts-expect-error: Unreachable code error
          (_REST.response && _REST.response.body) || {}, // eslint-disable-line
        ),
        queryStr = "%globals_server_QUERY_STRING^urldecode^striphtml%",
        vars = queryStr.split("&"),
        params = vars.reduce((p: { [key: string]: string }, pair) => {
          const parts = pair.split("=");
          if (parts.length === 2) {
            p[decodeURIComponent(parts[0])] = decodeURIComponent(parts[1]);
          }
          return p;
        }, {});
      /* eslint-disable*/ // @ts-ignore: Unreachable code error
      render(
        formData,
        // @ts-expect-error: Unreachable code error
        config,
        params,
        initialResults,
        (err: any, html: string) => {
          //config coming from squiz global-config.js
          // @ts-ignore: Unreachable code error
          print(err ? "err: " + err : html);
        },
      );
      /* eslint-enable*/
    } catch (e) {
      // @ts-expect-error: Unreachable code error
      print("e:" + e.name + ":" + e.message + " stack: " + e.stack);
    }
  };

  return {
    launch: launch,
  };
})();
