import React from "react";
import { renderToString } from "react-dom/server";
import type { MatrixConfigJSON } from "../shared/types/configJSON";
import type { Response } from "../shared/types/FBresponse";
import type { FinderProps } from "../shared/types/forms/finder";

export const render = (
  formData: React.ComponentType<FinderProps>,
  config: MatrixConfigJSON,
  initialQuery: { [key: string]: string },
  initialResults: Response,
  cb: ((err: unknown, html: string) => void)
) => {
  const Component = formData,
    ServerRenderer = (
      <Component
        config={config}
        initialQuery={initialQuery}
        initialResults={initialResults}
        element={null}
      />
    ),
    formHtml = renderToString(ServerRenderer);

  cb(null, formHtml);
};
